<template>
  <div id="app">
  <div class="container">
    <div class="header">
      <h2>Device Status</h2>
    </div>

    <div class="item">
      <label>Device</label>
      <label class="status">Status</label>
    </div>

    <div v-for="device in devices" :key="device.title">
    <hr/>
      <div class="item">
      <label>{{device.title}}</label>
      <img class="status" src="./assets/on_line.png" v-if="device.online"/>
      <img class="status" src="./assets/off_line.png" v-else/>
    </div>
    </div>
  </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'App',
  data(){
    return{
      devices:{}
    }
  },
  created(){
      axios.get('http://120.78.139.38:8080/all')
      .then((response) => {
            var dev_status = JSON.parse(response.data);
            console.log("The data is:")
            this.devices = dev_status.data.devices;
            console.log(this.devices);
            for(var dev in this.devices){
              console.log(typeof  this.devices[dev].online);
            }
				})
      .catch(function (error) {
            if (error.response) {
              console.log("get is sended");
            // 请求已发出，但服务器响应的状态码不在 2xx 范围内
            console.log(error.response.data);
            console.log(error.response.status);
            console.log(error.response.headers);
             } else {
            // Something happened in setting up the request that triggered an Error
            console.log("get not send");
            console.log('Error', error.message);
            }
           console.log(error.config);
      });
  }
}
</script>

<style>
#app {
   /* font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center; */
  /* color: #2c3e50; */
  background-color: blanchedalmond;
  width: 100%;
  /* height:500px; */
  /* margin-top: 60px;  */
  margin:0px 0px 0px 0px ;
  /* border-top:100px; */
  padding-top: 70px;
  padding-bottom: 100px;
}

.container{
  max-width: 1000px;
  margin: 0 auto;
  /* height: 200px; */
  border: 1px solid;
  background-color:beige;
  padding:0px 70px 0px 70px;
  padding-bottom: 50px;
}

.header{
  text-align: center;
  margin-bottom: 50px;
}

.item{
  font-size: 32px;
}

.status{
  float:right;
}
img{
  display: inline;
}
</style>



